<template>
  <div id='list-cows'>
    <h2>Zoznam krav</h2>
    <div class="container" id="list-container"> 
      <ul>
        <li v-for="cow in cows" class="list-item">
          <router-link to="{name: 'viewCow'}">
            Cislo:{{ cow.number }}
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
  export default {
    name: 'listCows',
    data() {
      return {
        cows: []
      }
    },
    created() {
      var docRef = db.collection("kravy");

      docRef.get().then(querySnapshot=> {
        querySnapshot.forEach(doc=>{
          const data = {
            'number': doc.data().number,
            'sex': doc.data().sex,
            'race': doc.data().race,
            'mastal': doc.data().mastal,
            'born': doc.data().born
          }
          this.cows.push(data);
        })
      });
    }
  }
</script>
<style lang="scss" scoped>
  h2 {
    padding: 20px 0;
    text-align: center;
  }

  #list-container {
    border: 2px solid grey;
  }

  .list-item {
    border-bottom: 1px solid grey;

    &:last-of-type {
      border: none;
    }

    a {
      display: block;
      padding: 5px;
      box-sizing: border-box;
    }
  }
</style>



