<template>
  <div class="header">
    <h3>
      <router-link to="/">{{ name }}</router-link>
    </h3>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: 'Cattle Manager'
      }
    }
  }
</script>

<style lang="scss" scoped>
// variables-colors

  $bgColorBasic: rgb(99, 158, 216);

  .header {
    text-align: center;

    h3 {
      padding: 15px;
      background-color: #fff;
      background-color: $bgColorBasic;
    }
  }
</style>




