<template>
  <div id='dashboard'>
    <div id="navigation-panel">
      <ul>
        <li>
          <router-link to="/new">Pridat kravu</router-link>
        </li>
        <li>
          <router-link to="/list">Zoznam krav</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'dashboard',
    data() {
      return {
        name: 'Dashboard for Cattle Management'
      }
    }
  }
</script>
<style lang="scss" scoped>
  // colors

  $inputColor: rgb(206, 200, 200);


  #dashboard {
    padding: 30vh 0;
  }

  #navigation-panel {
    max-width: 400px;
    margin: 0 auto;
    padding: 15px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px grey;

    li {
      margin: 15px;
      text-align: center;
      background-color: $inputColor;
      font-weight: bold;

      a {
        padding: 10px;
        display: block;
      }

      &:hover {
        background-color: darken($inputColor, 15%);
      }
    }
  }
</style>



