import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import EditCow from '@/components/EditCow'
import NewCow from '@/components/NewCow'
import ViewCow  from '@/components/ViewCow'
import ListCows  from '@/components/ListCows'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/edit/:id',
      name: 'edit-cow',
      component: EditCow
    },
    {
      path: '/new',
      name: 'new-cow',
      component: NewCow
    },
    {
      path: '/list',
      name: 'list-cows',
      component: ListCows
    },
    {
      path: '/:id',
      name: 'view-cow',
      component: ViewCow
    }
  ]
})



