<template>
  <div id='new-cow'>
    <h2>Pridat novu kravu</h2>
    <form class="container" id="add-form" @submit.prevent="addCow">
      <h3>Cislo kravy</h3>
      <input type="text" name="number" placeholder="cislo kravy" v-model="number">
      <h3>Datum narodenia</h3>
      <input type="text" name="number" placeholder="datum narodenia" v-model="born">
      <h3>Pohlavie</h3>
      <input type="text" name="number" placeholder="pohlavie" v-model="sex">
      <h3>Plemeno</h3>
      <input type="text" name="number" placeholder="plemeno" v-model="race">
      <h3>Mastal</h3>
      <input type="text" name="number" placeholder="mastal" v-model="mastal">
      <button class="btn" type="submit">Potvrdit</button>
    </form>
  </div>
</template>

<script>
import db from './firebaseInit'
  export default {
    name: 'newCow',
    data() {
      return {
        number: null,
        born: null,
        sex: null,
        race: null,
        mastal: null
      }
    },
    methods: {
      addCow(){
        db.collection('kravy').add({
          number: this.number,
          born: this.born,
          sex: this.sex,
          race: this.race,
          mastal: this.mastal
        }).then( docRef => {
          console.log('added!');
        })
        this.number = '';
        this.born = '';
        this.sex = '';
        this.race = '';
        this.mastal = '';
      }
    }
  }
</script>

<style lang="scss" scoped>
  #new-cow {
    text-align: center;

    h2 {
      padding: 30px 0;
    }
  }

  #add-form {

    h3 {
      margin: 15px 0;
      text-align: left;
    }

    input {
      width: 100%;
      padding: 5px;
      font-size: 1em;
      border-radius: 6px;
      border: 1px solid grey;

      &:last-of-type {
        margin-bottom: 25px;
      }
    }
  }
</style>



