<template>
  <div id="app">
    <Header></Header>
    <router-view/>
  </div>
</template>

<script>
import Header from "./components/Header"
export default {
  name: 'App',
  components: {
    Header
  }
}
</script>

<style lang="scss">

  @import url('https://fonts.googleapis.com/css?family=Montserrat:400,700');

  // variables-fonts

  $fontBasic: 'Montserrat', sans-serif;

  // colors
  

  // Globals

  * {
    padding: 0;
    margin: 0;
    font-family: $fontBasic;
    box-sizing: border-box;
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  button, input, textarea {
    border: none;
  }

  li {
    list-style: none;
  }

  img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;  
  }

  .container {
    max-width: 600px;
    margin: 0 auto;
    padding: 0 15px;
  }

  .btn {
    padding: 10px;
    background-color: transparent;
    border: 2px solid dodgerblue;
    color: dodgerblue;
    font-weight: bold;
    font-size: 1em;
    border-radius: 6px;
    width: 100%;
    cursor: pointer;
    transition: .2s ease;

    &:hover {
      background-color: dodgerblue;
      color: #fff;
    }
  }

</style>




